import { useSnackbar } from 'notistack';
import {  useEffect, useState, useMemo } from 'react';
// form
import { useForm } from 'react-hook-form';
// @mui
import { Card, Stack, Typography } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import axios from '../../../../utils/axios';
// components
import { FormProvider, RHFSwitch } from '../../../../components/hook-form';

// ----------------------------------------------------------------------
const SME = [
 {value: 'mtn_sme', label: 'MTN SME VENDING'},
 {value: 'airtel_sme', label: 'AIRTEL SME VENDING'},
 {value: 'glo_sme', label: 'GLO SME VENDING'},
 {value: 'mobile_sme', label: '9MOBILE SME VENDING'}
];

const CG = [
  {value: 'mtn_cg', label: 'MTN CG VENDING'},
  {value: 'airtel_cg', label: 'AIRTEL CG VENDING'},
  {value: 'glo_cg', label: 'GLO CG VENDING'},
  {value: 'mobile_cg', label: '9MOBILE CG VENDING'}
 ];
 const G = [
  {value: 'mtn_g', label: 'MTN GIFTING VENDING'},
  {value: 'airtel_g', label: 'AIRTEL GIFTING VENDING'},
  {value: 'glo_g', label: 'GLO GIFTING VENDING'},
  {value: 'mobile_g', label: '9MOBILE GIFTING VENDING'}
 ];



// ----------------------------------------------------------------------

export default function AccountNotifications() {
  const [stock, setStock] = useState('');
  const { enqueueSnackbar } = useSnackbar();
  const [load, SetLoad] = useState()
  const AccessToken =  window.localStorage.getItem('accessToken');
  const defaultValues = useMemo(
    () => ({
    mtn_sme: stock?.mtn_sme,
    airtel_sme: stock?.airtel_sme,
    glo_sme: stock?.glo_sme,
    mobile_sme: stock?.mobile_sme,

    mtn_cg: stock?.mtn_cg,
    glo_cg: stock?.glo_cg,
    airtel_cg: stock?.airtel_cg,
    mobile_cg: stock?.mobile_cg,

    mtn_g: stock?.mtn_g,
    glo_g: stock?.glo_g,
    airtel_g: stock?.airtel_g,
    mobile_g: stock?.mobile_g
  }),
  // eslint-disable-next-line react-hooks/exhaustive-deps
  [stock]
);

  const methods = useForm({
    // defaultValues,
  });
 useEffect(() => {
  SetLoad(true);
  axios.get(`/api/user/stock/wallet/${AccessToken}/secure/Habukhan`).then(data =>{
     setStock(data.data?.stock);
     SetLoad(false);
  });
}, [AccessToken]);
  const {
    handleSubmit,
    reset,
    formState: { isSubmitting },
  } = methods;

  useEffect(() => {
    if (stock) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [stock]);
  const onSubmit = async (data) => {
    try {
     await axios.post(`/api/user/edit/stockvending/${AccessToken}/system/secure`,data)
      enqueueSnackbar('Update success!');
    } catch (error) {
      if(error.data?.message !== undefined){
        enqueueSnackbar(error.data?.message, { variant: 'error' })  
         } else if(error?.message !== undefined){
            enqueueSnackbar(error?.message, { variant: 'error' })   
         }else{
            enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
         }
 }
}

  return (
    <Card sx={{ p: 3 }}>
      <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      {!load? <><Stack spacing={3} alignItems="flex-end">
          <Stack spacing={2} sx={{ width: 1 }}>
            <Typography variant="overline" sx={{ color: 'text.secondary' }}>
              SME
            </Typography>

            <Stack spacing={1}>
              {SME.map((activity) => (
                <RHFSwitch key={activity.value} name={activity.value} label={activity.label} sx={{ m: 0 }} />
              ))}
            </Stack>
          </Stack>

          <Stack spacing={2} sx={{ width: 1 }}>
            <Typography variant="overline" sx={{ color: 'text.secondary' }}>
              COOPERATE GIFTING
            </Typography>
            <Stack spacing={1}>
              {CG.map((application) => (
                <RHFSwitch key={application.value} name={application.value} label={application.label} sx={{ m: 0 }} />
              ))}
            </Stack>
          </Stack>

          <Stack spacing={2} sx={{ width: 1 }}>
            <Typography variant="overline" sx={{ color: 'text.secondary' }}>
               GIFTING
            </Typography>
            <Stack spacing={1}>
              {G.map((application) => (
                <RHFSwitch key={application.value} name={application.value} label={application.label} sx={{ m: 0 }} />
              ))}
            </Stack>
          </Stack>
          <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
            Save Changes
          </LoadingButton>
        </Stack></>:  <Typography variant="h4">Loading ..</Typography> }
      </FormProvider>
    </Card>
  );
}
