import * as Yup from 'yup';
import { useSnackbar } from 'notistack';
import { useCallback } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { Box, Grid, Card, Stack, Typography, } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// hooks
import useAuth from '../../../../hooks/useAuth';
// utils
import { fData } from '../../../../utils/formatNumber';
// components
import Label from '../../../../components/Label';
import axios from '../../../../utils/axios';
// components
import { FormProvider,  RHFTextField, RHFUploadAvatar } from '../../../../components/hook-form';
import SettingMode from '../../../../components/settings/SettingMode';
import SettingFullscreen from '../../../../components/settings/SettingFullscreen';

// ----------------------------------------------------------------------

export default function AccountGeneral() {
  const { enqueueSnackbar } = useSnackbar();
  const { user } = useAuth();
  const AccessToken =  window.localStorage.getItem('accessToken');
  const UpdateUserSchema = Yup.object().shape({
    fullname: Yup.string().required('Name is required'),
  });
 

  const defaultValues = {
    fullname: user?.name || '',
    email: user?.email || '',
    profile_image: user?.profile_image || '',
    username: user?.username || '',
    phoneNumber: user?.phone || '',
    address: user?.address || '',
    webhook: user?.webhook || '',
    about: user?.about || '',
    type: user?.type || ''
  };

  const methods = useForm({
    resolver: yupResolver(UpdateUserSchema),
    defaultValues,
  });

  const {
    setValue,
    watch,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
 const values = watch();
  const onSubmit = async (data) => {
    // eslint-disable-next-line prefer-const
    let formData = new FormData();
        // eslint-disable-next-line no-restricted-syntax, guard-for-in
        for (const key in data) {
            formData.append(key, data[key]);
        }
    try {
      await axios.post(`/api/edituser/system/secure/${AccessToken}/secure`, formData)
      enqueueSnackbar('Update success!');
    } catch (error) {
      if(error.data?.message !== undefined){
        enqueueSnackbar(error.data?.message, { variant: 'error' })  
         } else if(error?.message !== undefined){
            enqueueSnackbar(error?.message, { variant: 'error' })   
         }else{
            enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
         }
    }
  };


  const handleDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];

      if (file) {
        setValue(
          'profile_image',
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        );
      }
    },
    [setValue]
  );

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Card sx={{ py: 10, px: 3, textAlign: 'center' }}>
            <RHFUploadAvatar
              name="profile_image"
              accept="image/*"
              maxSize={3145728}
              onDrop={handleDrop}
              helperText={
                <Typography
                  variant="caption"
                  sx={{
                    mt: 2,
                    mx: 'auto',
                    display: 'block',
                    textAlign: 'center',
                    color: 'text.secondary',
                  }}
                >
                  Allowed *.jpeg, *.jpg, *.png, *.gif
                  <br /> max size of {fData(3145728)}
                </Typography>
              }
            />
            <Label
               
               color={'success'}
               sx={{ textTransform: 'uppercase', position: 'absolute', top: 24, right: 24 }}
             >
               {values.type}
             </Label>
             <Stack spacing={1.5}>
            <SettingMode />
            <SettingFullscreen />
            </Stack>
          </Card>
        </Grid>

        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                rowGap: 3,
                columnGap: 2,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField name="fullname" label="Full Name"  disabled/>
              <RHFTextField name="username" label="UserName"  disabled/>
              <RHFTextField name="email" label="Email Address"  disabled/>
              <RHFTextField name="phoneNumber" label="Phone Number" disabled/>

              <RHFTextField name="address" label="Address" />
              <RHFTextField name="webhook" label="Webhook URL" />
              
            </Box>

            <Stack spacing={3} alignItems="flex-end" sx={{ mt: 3 }}>
              <RHFTextField name="about" multiline rows={4} label="About" />

              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                Save Changes
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
