import * as Yup from 'yup';
import { useState, useEffect, useMemo } from 'react';
import { useSnackbar } from 'notistack';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';

// @mui
import { Box, Grid, Card, Typography, Stack } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// components
import { FormProvider,  RHFTextField, RHFSelect } from '../../../../components/hook-form';
// ----------------------------------------------------------------------
import axios from '../../../../utils/axios';



export default function AccountBilling() {
 const [account, SetAccount] = useState('')
 const [code, Setcode] = useState('');
 const [bank, SetBank] = useState([]);
 const [load, SetLoad] = useState();
 const [bankname, Setbankname] = useState('');
 const { enqueueSnackbar } = useSnackbar();

 const AccessToken =  window.localStorage.getItem('accessToken');
 const UpdateUserSchema = Yup.object().shape({
   account_number: Yup.string().required('Account Number required'),
 });

 const defaultValues = useMemo(
  () => ({
    account_name: account?.bank_name,
    account_number: account?.account_number,
}),
  // eslint-disable-next-line react-hooks/exhaustive-deps
[account]
);

 const methods = useForm({
   resolver: yupResolver(UpdateUserSchema),
 });
 useEffect(() => {
  SetLoad(true);
  axios.get('https://api.paystack.co/bank').then(data => {SetBank(data?.data?.data);  SetLoad(false);})
  axios.get(`/api/user/accountdetails/wallet/${AccessToken}/secure/Habukhan`).then(data =>{
    SetAccount(data.data?.bank);
    SetLoad(false);
 });
 
}, [AccessToken]);
 const {
  reset,
  watch,
   handleSubmit,
   formState: { isSubmitting },
 } = methods;
 useEffect(() => {
  if(account){
    reset(defaultValues);
    Setbankname(account?.bank);
    Setcode(account?.bank_code);
  }
 
// eslint-disable-next-line react-hooks/exhaustive-deps
}, [account]);
 const values = watch();
 const onSubmit = async (data) => {
   try {
   
     await axios.post(`/api/accountdetails/system/secure/${AccessToken}/secure`,{
      account_name : data.account_name,
      bank_name: bankname,
      bank_code: code,
      account_number: data.account_number
     });
     axios.get(`/api/user/accountdetails/wallet/${AccessToken}/secure/Habukhan`).then(data =>{
      SetAccount(data.data?.bank);
   });
     enqueueSnackbar('Update success!');
   } catch (error) {
     if(error.data?.message !== undefined){
       enqueueSnackbar(error.data?.message, { variant: 'error' })  
        } else if(error?.message !== undefined){
           enqueueSnackbar(error?.message, { variant: 'error' })   
        }else{
           enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
        }
   }
 };

 const bankchange = async(data) => {
  Setbankname(data)
  const  value = bank.filter((item) => item.name === data)
  if(value[0]){
  Setcode(value[0].code)
  }
 }
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
    <Grid container spacing={5}>
      <Grid item xs={12} md={8}>
        <Stack spacing={3}>
          <Card sx={{ p: 3 }}>
            <Typography variant="h4">Account Details</Typography>
            <Box
              sx={{
                display: 'grid',
                rowGap: 3,
                columnGap: 2,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              {!load ? <> <RHFSelect name="bank_name" label="Bank Name" onChange={(data)=> bankchange(data.target.value)} value={bankname}>
                <option value="" />
                {bank.map((option) => (
                  <option key={option?.code} value={option?.name}>
                    {option?.name}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="account_number" label="Account Number" defaultValue="Account Number"/>
             {values?.account_name !== undefined && <RHFTextField name="account_name" label="Account Name"  defaultValue disabled/>}
            </> :  <Typography variant="h4">Loading ..</Typography> }
            </Box>
           {!load && <Stack spacing={3} alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                Save Changes
              </LoadingButton>
            </Stack> }
          </Card>

          
        </Stack>
      </Grid>
    </Grid>
    </FormProvider>
  );
}
